import React, { Component } from 'react'

import '../assets/scss/BurgerMenu.scss'

import burger from '../assets/img/burger.png'
// import burgerWhite from '../assets/img/burger-white.png'


import NavMenu from '../components/NavMenu'


export default class Services extends Component {

    state ={
        showMenu: false,
      }

      toggleBurger = () =>{
        this.setState(prevState => ({
          showMenu: !prevState.showMenu
        }))
      }

    render(){
      const {showMenu} = this.state;

        return (
            <div className="BurgerMenu">
                    {/* <div className="AboutUs-menu">  */}
                                    {/* <button onClick={this.toggleBurger}> */}
                <div className="BurgerMenu__burger"  onClick={this.toggleBurger}>
                
                    <img src={burger} className="burger" alt="logo" />
                    {/* <img src={burgerWhite} className="burgerWhite" alt="logo" /> */}
                    
                </div>
                    {/* </button> */}
                {/* </div> */}

                {showMenu && (
                    <div className="BurgerMenu__open"> 
                    <NavMenu />
                    </div>

                )}
            {/* </div> */}
            </div>
        )
    }
}