import React, { Component } from 'react'
// import NavMenu from '../components/NavMenu'
import '../assets/scss/Contact.scss'
// import MainLogo from '../components/MainLogo'
import BurgerMenu from '../components/BurgerMenu'
import ArroWhite from '../assets/img/arrow-white.png'
import ContactForm from '../components/ContactForm';

export default class Contact extends Component {

    state = {
        showContactForm: true,
    }

    toggleSliderForm = () => {
        this.setState(prevState => ({
            showContactForm: !prevState.showContactForm
        }))
    }
    render() {
        
        const {showContactForm} = this.state 

        return(
            <div className="Contact"> 
                <div className="Contact-Description"> 
                    <div className="Contact-Description__Title"> 
                    <p>
                        <strong>Boots Me</strong> - рекламная агенція. <br/>
                        Ми нове покоління, яке створює рекламу на рівні підсвідомості.
                    </p>
                    </div>
                    <div className="Contact-Description__Arrow"
                    onClick={this.toggleSliderForm}>
                        <img src={ArroWhite} alt="arrow"/>
                        <img src={ArroWhite} alt="arrow"/>

                    </div> 
                </div>
               {showContactForm && (
                <div className="Contact-Form"> 
                    <div className="Contact-Form_Fill-Field"> 
                        <div className="Contact-Form_Fill-Field__Navigation"> 
                            <BurgerMenu/>
                        </div>
                        <div className="Contact-Form_Fill-Field__Info"> 
                            <p>Бажаєте обговорити проект ?</p>

                            <ContactForm/>
                        </div>
                            <div className="Contact-Form_Fill-Field__Number"> 
                                <a href="tel:+ 380678555558">(067) 855 5558</a>
                            </div>
                    </div>
                </div>

               )}
                          
            </div>
        )
    }
}
