import React, {Component} from 'react'

import logo from '../assets/img/MainLogo.png'
import logoMob from '../assets/img/logo-mob.png'

// import '../assets/scss/MainLogo.scss'

export default class MainLogo extends Component {
    render() {
        return(
            <div className="Main-header">
                <div className="Main-header_logo">
                    <img src={logo} className="main-logo" alt="logo" />
                    
                    <img src={logoMob} className="main-logo-mob" alt="logo" />
                                
                </div>
            </div>
        )
    }
}